import React, { lazy, Suspense, useEffect } from 'react';
import { Routes, Route, useLocation } from 'react-router-dom';
import { LanguageProvider } from './context/LanguageContext';
import ImagePreloader from './components/ImagePreloader';
import './App.css';

// Use React.lazy for code splitting
const Home = lazy(() => import('./pages/index'));
const About = lazy(() => import('./pages/about'));

// Simple loading spinner
const LoadingSpinner = () => (
  <div className="flex h-screen w-screen items-center justify-center bg-sayqal-cream">
    <div className="h-16 w-16 animate-spin rounded-full border-4 border-solid border-sayqal-gold border-t-transparent"></div>
  </div>
);

function App() {
  const location = useLocation();
  
  // Scroll to top when route changes
  useEffect(() => {
    // Use setTimeout to allow UI to render first
    setTimeout(() => {
      // Check if there's a hash in the URL
      if (location.hash) {
        const id = location.hash.substring(1);
        const element = document.getElementById(id);
        if (element) {
          element.scrollIntoView({ behavior: 'smooth', block: 'start' });
        }
      } else {
        // Smooth scroll to top with improved performance
        window.scrollTo({
          top: 0,
          left: 0,
          behavior: 'smooth'
        });
      }
    }, 0);
  }, [location.pathname]);
  
  // Fix for scroll lag - prevent unnecessary repaints
  useEffect(() => {
    const style = document.createElement('style');
    style.innerHTML = `
      * {
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        -ms-backface-visibility: hidden;
        backface-visibility: hidden;
      }
    `;
    document.head.appendChild(style);
    
    return () => {
      document.head.removeChild(style);
    };
  }, []);

  return (
    <LanguageProvider>
      <ImagePreloader />
      <Suspense fallback={<div className="loading-container"><div className="loading-spinner"></div></div>}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/about" element={<About />} />
        </Routes>
      </Suspense>
    </LanguageProvider>
  );
}

export default App;
